.hero {
  padding-top: 70px;
  background-color: #f1f1f1;

  @media (max-width: 1000px) {
    padding-top: 50px;
  }

  @media (max-width: 700px) {
    padding-top: 0;
  }
  &__container {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 176px;

    @media (max-width: 1000px) {
      flex-direction: column;
      row-gap: 30px;
    }
  }

  &__text {
    max-width: 500px;
    padding-top: 124px;

    @media (max-width: 1000px) {
      padding-top: 0;
    }
  }

  &__title {
    max-width: 482px;
    margin: 0;

    font-weight: 600;
    font-size: 44px;
    color: #000;

    @media (max-width: 700px) {
      max-width: 300px;
      font-size: 34px;
    }
  }

  &__title span {
    @media (max-width: 700px) {
      font-size: 24px;
    }
  }

  &__subtitle {
    margin: 0;
    padding-top: 20px;

    font-weight: 400;
    font-size: 14px;
    line-height: 200%;
    color: #000;
  }

  &__image {
    @media (max-width: 1000px) {
      width: 100%;
      text-align: center;
      align-items: center;
      justify-content: center;
    }
  }
}
