.work {
  background-color: #f1f1f1;
  
  &__container {
  }

  &__title {
    margin: 0;
    padding-bottom: 70px;

    font-weight: 600;
    font-size: 34px;
    text-align: center;
    color: #070707;

    @media (max-width: 700px) {
      padding-bottom: 50px;
    }
  }

  &__content {
  }

  &__list {
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    &:not(:last-child) {
      padding-bottom: 90px;

      @media (max-width: 700px) {
        padding-bottom: 50px;
      }
    }
  }

  &__link {
    margin: 0;
    padding-top: 40px;

    font-weight: 400;
    font-size: 18px;
    text-decoration: underline;
    text-align: center;
    color: #070707;

    @media (max-width: 700px) {
      padding-top: 30px;
      color: #000;
    }

    @media (max-width: 425px) {
      padding-bottom: 10px;
    }
  }
}
