.header {
  padding: 40px 0;
  background-color: #f1f1f1;

  &__container {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
  }

  &__navbar {
    @media (max-width: 768px) {
      position: fixed;
      top: 0;
      left: -100%;
      overflow-y: auto;
      z-index: 100;
      width: 80%;
      max-width: 375px;
      height: 100vh;
      background-color: #fff;
      transition: opacity 0.3s, visibility 0.3s, transform 0.3s, left 0.3s;
      box-shadow: 10px 0 23px -5px rgba(34, 60, 80, 0.2);
    }
  }
}

.menu--active {
  left: 0;
}

.navbar {
  &__list {
    display: flex;
    gap: 50px;

    @media (max-width: 768px) {
      padding: 50px;
      display: flex;
      flex-direction: column;
      gap: 30px;
    }
  }

  &__item {
  }

  &__link {
    font-weight: 600;
    font-size: 14px;
    color: #000;
    transition: 0.3s;

    @media (max-width: 768px) {
      font-weight: 600;
      font-size: 34px;
      color: #090909;
    }

    @include hover {
      color: #606060;
    }
  }
}
