.development {
  &__container {
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;

    @media (max-width: 700px) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
  }

  &__item {
    grid-column: 4 span;
  }

  &__title {
    margin: 0;
    padding-top: 30px;
    padding-bottom: 10px;

    font-weight: 600;
    font-size: 24px;
    text-transform: uppercase;
    color: #000;
  }

  &__info {
    margin: 0;

    font-weight: 400;
    font-size: 14px;
    line-height: 200%;
    color: #000;
  }
}
