.footer {
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__subtitle {
    margin: 0;

    font-weight: 600;
    font-size: 34px;
    text-align: center;
    color: #070707;
  }

  &__question {
    margin: 0;
    padding: 40px 0;

    font-weight: 400;
    font-size: 18px;
    text-align: center;
    color: #070707;

    @media (max-width: 700px) {
      padding: 20px 0;
    }
  }

  &__button {
    max-width: 180px;
    padding: 15px 20px;
    border-radius: 20px;

    font-weight: 400;
    font-size: 18px;
    text-align: center;
    color: #fff;
    border: 1px solid #070707;
    background-color: #070707;

    @include hover {
      background-color: #fff;
      color: #070707;
    }
  }

  &__social {
    padding: 80px 0 40px;
    display: flex;
    justify-content: center;
    gap: 61px;

    @media (max-width: 700px) {
      padding: 70px 0 20px;
      gap: 40px;
    }
  }

  &__descr {
    margin: 0;
    max-width: 260px;

    font-weight: 400;
    font-size: 14px;
    text-align: center;
    color: #828282;
  }
}

.social {
  &__item {
  }

  &__link {
    display: block;
    width: 40px;
    height: 40px;
    background-position: center center;
    background-repeat: no-repeat;
  }

  &__link-linked {
    background-image: url("../img/LinkedIn.svg");
  }

  &__link-insta {
    background-image: url("../img/Instargam.svg");
  }

  &__link-behance {
    background-image: url("../img/Behance.svg");
  }

  &__link-dribble {
    background-image: url("../img/Dribble.svg");
  }
}
